    monospace;
}

* {
  font-family: -apple-system, Arial, sans-serif;
  padding: 0;
  margin: 0;
}

body {
  color: black;
  background-color: white;
}

.hide {
  display: none !important;
}

.flex-br {
  width: 100%;
  height: 0;
}

.grid-container {
